import React from 'react'

import { ShopClosingTimeModal } from 'segments/shop/ShopClosingTimeModal'

import { QueryMobileShopStatus } from './MobileClosingTimeModal.graphql'

interface IProps {
  branchId: ID
}

export const MobileClosingTimeModal: React.SFC<IProps> = ({ branchId }) => (
  <QueryMobileShopStatus
    variables={{ branchId }}
    render={({ branch }) => <ShopClosingTimeModal status={branch} />}
  />
)
