import React from 'react'
import { Field } from 'react-form'

import { SelectButtonThemed } from 'common/Buttons'
import SubmitSpinner from 'common/SubmitSpinner'
import { WithOrder } from 'common/WithOrder'
import { PaymentGateEnum } from 'graph/types'

interface IProps {
  method: PaymentGateEnum
  disabled?: boolean
  onClick(): void
}

export const MobilePaymentButton: React.SFC<IProps> = ({
  method,
  disabled,
  onClick,
  children,
  ...props
}) => (
  <WithOrder
    observe={true}
    render={order => (
      <Field
        field="payment"
        render={fieldApi => (
          <SelectButtonThemed
            selected={fieldApi.value === method}
            disabled={disabled || order.isSubmitting}
            onClick={() => {
              fieldApi.setValue(method)
              onClick()
            }}
            {...props}
          >
            {order.isSubmitting && fieldApi.value === method ? (
              <SubmitSpinner />
            ) : (
              children
            )}
          </SelectButtonThemed>
        )}
      />
    )}
  />
)
