import { i18n } from 'i18n'
import React, { PureComponent } from 'react'
import { Marker } from 'react-google-maps'
import styled from 'styled-components'

import { BaseMap } from 'common/BaseMap'
import { PrimaryButton } from 'common/Buttons'
import ButtonStripe from 'common/ButtonStripe'
import { GPickupBranch } from 'graph/types'
import { gpsToLatLng } from 'helpers/gps'
import { TMapModel } from 'models/MapModel'

interface IProps {
  model: TMapModel
  branch: Maybe<GPickupBranch.Fragment>
  onSelect(): void
}

const ButtonStripeContainerStyled = styled.div`
  margin-top: 2rem;
  z-index: 10;
`

class MobilePickupMap extends PureComponent<IProps> {
  componentDidMount() {
    this.panToBranch()
  }
  componentDidUpdate() {
    this.panToBranch()
  }
  panToBranch() {
    const { model, branch } = this.props
    if (branch) {
      model.panToLocation(branch.gps)
    }
  }
  render() {
    const { model, branch, onSelect } = this.props
    const latlng = branch
      ? gpsToLatLng({
          latitude: Number(branch.gps.latitude),
          longitude: Number(branch.gps.longitude),
        })
      : undefined
    return (
      <div>
        <div className="mobile__map__view">
          <BaseMap model={model} render={() => <Marker position={latlng} />} />
        </div>
        {branch &&
          branch.pickupEnabled && (
            <ButtonStripeContainerStyled>
              <ButtonStripe>
                <PrimaryButton
                  onClick={onSelect}
                  data-testid="mobilePickupMap__SubmitButton"
                >
                  {i18n.t`Choose branch`.toLocaleUpperCase()}
                </PrimaryButton>
              </ButtonStripe>
            </ButtonStripeContainerStyled>
          )}
        {branch &&
          !branch.pickupEnabled && (
            <ButtonStripeContainerStyled>
              <ButtonStripe>
                <PrimaryButton
                  disabled={true}
                  data-testid="mobilePickupMap__SubmitButton"
                >
                  {i18n.t`Delivery only`.toLocaleLowerCase()}
                </PrimaryButton>
              </ButtonStripe>
            </ButtonStripeContainerStyled>
          )}
      </div>
    )
  }
}

export default MobilePickupMap
