import './MobileMap.css'

import { History } from 'history'
import { i18n } from 'i18n'
import React from 'react'
import { RouteComponentProps } from 'react-router'

import { BaseMap } from 'common/BaseMap'
import { WithRoot } from 'common/WithRoot'
import { DeliveryMap } from 'segments/map/DeliveryMap'

import MobileHeader from './MobileHeader'

const LocationDiscovery = () => <i>{i18n.t`Finding location...`}</i>

interface IProps extends RouteComponentProps<{}> {
  history: History
}

export const MobileDeliveryView: React.SFC<IProps> = ({ history }) => (
  <div>
    <MobileHeader
      text={i18n.t`Enter the address`}
      buttonText={i18n.t`Back`}
      onButtonClick={() => history.goBack()}
    />
    <div className="mobile__map__view">
      <WithRoot
        render={root =>
          root.location.hasLocation ? (
            <BaseMap
              model={root.map}
              render={() => (
                <DeliveryMap
                  onConfirm={() => history.push('/order')}
                  rootModel={root}
                />
              )}
            />
          ) : (
            <LocationDiscovery />
          )
        }
      />
    </div>
  </div>
)
