import React from 'react'

import { TBusinessModel } from '../models/BusinessModel'
import { WithRoot } from './WithRoot'

interface IProps {
  render(model: TBusinessModel): ReactNode | ReactNode[]
  observe?: boolean
}

export const WithBusiness: React.SFC<IProps> = ({ render, observe }) => (
  <WithRoot
    observe={observe}
    render={({ business }) => (business.isValid ? render(business) : null)}
  />
)
