import React from 'react'
import { Route } from 'react-router-dom'
import { Container } from 'reactstrap'

import { MobileConfirmView } from 'segments/mobile/MobileConfirmView'
import { MobileDeliveryView } from 'segments/mobile/MobileDeliveryView'
import { MobileIntroView } from 'segments/mobile/MobileIntroView'
import { MobileOrderView } from 'segments/mobile/MobileOrderView'
import { MobilePickupView } from 'segments/mobile/MobilePickupView'
import { MobileSmartBanner } from 'segments/mobile/MobileSmartBanner'

import { MobileFinishView } from '../segments/mobile/MobileFinishView'

const MobileApp = () => (
  <>
    <Route path="/" component={MobileSmartBanner} />
    <Container fluid={true}>
      <Route exact={true} path="/" component={MobileIntroView} />
      <Route path="/pickup/:branchId?" component={MobilePickupView} />
      <Route path="/delivery" component={MobileDeliveryView} />
      <Route path="/order" component={MobileOrderView} />
      <Route path="/confirm" component={MobileConfirmView} />
      <Route path="/finish" component={MobileFinishView} />
    </Container>
  </>
)

export default MobileApp
