import './MobileConfirmView.css'

import { Trans } from '@lingui/react'
import { i18n } from 'i18n'
import React from 'react'
import { RouteComponentProps } from 'react-router'

import { ScrollToTop } from '../../common/ScrollToTop'
import { WithOrder } from '../../common/WithOrder'
import { OrderAgreements } from '../orderForm/OrderAgreements'
import { OrderFormInputs } from '../orderForm/OrderFormInputs'
import { OrderFormSetup } from '../orderForm/OrderFormSetup'
import { OrderFormStorageNotice } from '../orderForm/OrderFormStorageNotice'
import MobileHeader from './MobileHeader'
import { MobilePaymentMethod } from './MobilePaymentMethod'

interface IProps extends RouteComponentProps<{}> {}

export const MobileConfirmView: React.SFC<IProps> = ({ history }) => (
  <div className="mobile__confirm__view">
    <MobileHeader
      text={i18n.t`Order confirmation`}
      buttonText={i18n.t`Cancel`}
      onButtonClick={() => history.goBack()}
    />
    <WithOrder
      render={order => (
        <OrderFormSetup
          order={order}
          render={form => (
            <>
              <ScrollToTop />
              <OrderFormStorageNotice />
              <div className="mobile__confirm__view__form">
                <h3>
                  <Trans>Where and to whom we deliver?</Trans>
                </h3>
                <OrderFormInputs />
                <OrderAgreements />
              </div>
              <div className="mobile__confirm__view__submit">
                <h4>
                  <Trans>Place order</Trans>
                </h4>
                <MobilePaymentMethod
                  orderModel={order}
                  onConfirm={form.submitForm}
                />
              </div>
            </>
          )}
        />
      )}
    />
  </div>
)
