import './MobileCartSummary.css'

import { Trans } from '@lingui/react'
import { i18n } from 'i18n'
import React from 'react'
import { styled } from 'styled'

interface IProps {
  count: number
  price: string
  isLoading: boolean
  onClick(): void
}

const MobileCartSummaryCountThemed = styled('div')`
  background-color: ${({ theme }) => theme.secondary};
`

const MobileCartSummaryPriceThemed = styled('span')`
  color: ${({ theme }) => theme.primary};
`

export const MobileCartSummary: React.SFC<IProps> = ({
  count,
  price,
  isLoading,
  onClick,
}) => {
  const showContent = count > 0 || isLoading === true
  return (
    <div
      className="mobile__cart__summary"
      data-testid="mobileCartSummary"
      onClick={onClick}
    >
      {showContent ? (
        <div className="mobile__cart__summary__toggle">
          <span className="flaticon-chevron-up" />
        </div>
      ) : null}

      <div className="mobile__cart__summary__info">
        <span className="flaticon-cart" />
        {showContent ? (
          <MobileCartSummaryCountThemed
            className="mobile__cart__summary__count"
            data-testid="mobileCartSummary__Count"
          >
            {count || '?'}
          </MobileCartSummaryCountThemed>
        ) : null}
        {!showContent ? (
          <span data-testid="cartIsEmpty">{i18n.t`Cart is empty`}</span>
        ) : (
          <div
            className="mobile__cart__summary__cart__with__price"
            data-testid="mobileCartSummary__Price"
          >
            <Trans>
              In the cart you have goods for
              <MobileCartSummaryPriceThemed className="mobile__cart__summary__price">
                {price}
              </MobileCartSummaryPriceThemed>
            </Trans>
          </div>
        )}
      </div>
    </div>
  )
}
