import { buildQuery, gql } from 'graph'

import { CompanyBranchStatusFragment } from 'graph/fragments.graphql'
import { GMobileShopStatus } from 'graph/types'

export const MobileShopStatusQuery = gql`
  query GMobileShopStatus($branchId: ID!) {
    branch: companyBranch(id: $branchId) {
      id
      ...GCompanyBranchStatus
    }
  }
  ${CompanyBranchStatusFragment}
`

export const QueryMobileShopStatus = buildQuery<
  GMobileShopStatus.Query,
  GMobileShopStatus.Variables
>(MobileShopStatusQuery)
