import gql from 'graphql-tag'

import { buildQuery } from 'graph/buildQuery'
import { GMobileShopIntro } from 'graph/types'

export const MobileShopIntroQuery = gql`
  query GMobileShopIntro($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      headerText
    }
  }
`

export const QueryMobileShopIntro = buildQuery<
  GMobileShopIntro.Query,
  GMobileShopIntro.Variables
>(MobileShopIntroQuery)
