import './MobileIntroView.css'

import React from 'react'
import { Route } from 'react-router-dom'

import BackgroundThemed from 'common/BackgroundThemed'
import { OrderMethod } from 'segments/destination/OrderMethod'
import { ShopHeaderIcons } from 'segments/shop/ShopHeaderIcons'

import { MobileShopIntro } from './MobileShopIntro'

export const MobileIntroView: React.SFC = () => (
  <div className="mobile__intro">
    <BackgroundThemed
      className="mobile__intro__logo"
      data-testid="MobileIntroView__BackgroundThemed"
      imageName="storeLogoMobile"
      size="cover"
      fixed={false}
    >
      <ShopHeaderIcons />
    </BackgroundThemed>
    <div className="mobile__intro__info">
      <MobileShopIntro />
      <Route>
        {({ history }) => (
          <OrderMethod
            onChange={order => {
              if (order.usingDeliveryOrderMethod) {
                history.push('/delivery')
              } else if (order.usingPickupOrderMethod) {
                history.push('/pickup')
              }
            }}
          />
        )}
      </Route>
    </div>
  </div>
)
