import React from 'react'

import { WithBusiness } from '../../common/WithBusiness'
import { QueryMobileShopIntro } from './MobileShopIntro.graphql'

interface IProps {}

export const MobileShopIntro: React.SFC<IProps> = props => (
  <WithBusiness
    observe={true}
    render={business => (
      <QueryMobileShopIntro
        variables={{ branchId: business.branchId! }}
        renderLoading={() => null}
        render={({ companyBranch: { headerText } }) => (
          <div data-testid="MobileShopIntro">{headerText}</div>
        )}
      />
    )}
  />
)
