import './MobileHeader.css'

import React from 'react'

import { MobileHeaderButton } from 'common/Buttons'

interface IProps {
  text: string
  buttonText: string
  buttonRight?: boolean
  onButtonClick(): void
}

const MobileHeader: React.SFC<IProps> = ({
  text,
  buttonText,
  buttonRight = false,
  onButtonClick,
}) => {
  const leftButton =
    buttonRight === false ? (
      <MobileHeaderButton
        className="mobile__header__button"
        data-testid="MobileHeaderButton__Left"
        // size="sm"
        onClick={onButtonClick}
      >
        {buttonText}
      </MobileHeaderButton>
    ) : null
  const rightButton =
    buttonRight === true ? (
      <MobileHeaderButton
        className="mobile__header__button"
        data-testid="MobileHeaderButton__Right"
        // size="sm"
        onClick={onButtonClick}
        isConfirm={true}
      >
        {buttonText}
      </MobileHeaderButton>
    ) : null
  return (
    <div className="mobile__header">
      {leftButton}
      <div className="mobile__header__text">{text}</div>
      {rightButton}
    </div>
  )
}

export default MobileHeader
