import { i18n } from 'i18n'
import React, { Component } from 'react'
import { RouteComponentProps } from 'react-router'

import { WithRoot } from 'common/WithRoot'
import { RM } from 'helpers/ramda'
import { TRootModel } from 'models/RootModel'

import { GPickupBranch } from '../../graph/types'
import {
  QueryPickupMapBranch,
  QueryPickupMapBrand,
} from '../map/PickupMap.graphql'
import MobileHeader from './MobileHeader'
import MobilePickupList from './MobilePickupList'
import MobilePickupMap from './MobilePickupMap'

interface IRouteParams {
  branchId?: string
}

interface IProps extends RouteComponentProps<IRouteParams> {}

export class MobilePickupView extends Component<IProps> {
  onSelectBranch(branchId: ID, root: TRootModel) {
    root.order.createPickupOrder(branchId, () =>
      this.props.history.push('/order'),
    )
  }
  renderHeader(branch: Maybe<GPickupBranch.Fragment>) {
    return (
      <MobileHeader
        text={branch ? branch.name : i18n.t`Choose branch`}
        buttonText={i18n.t`Back`}
        onButtonClick={() => this.props.history.goBack()}
      />
    )
  }
  renderMap(branch: Maybe<GPickupBranch.Fragment>, root: TRootModel) {
    return (
      <MobilePickupMap
        model={root.map}
        branch={branch}
        onSelect={() => branch && this.onSelectBranch(branch.id, root)}
      />
    )
  }
  renderList(branches: GPickupBranch.Fragment[], root: TRootModel) {
    return (
      <MobilePickupList
        branches={branches}
        onShowMap={({ id }) => this.props.history.push(`/pickup/${id}`)}
        onSelect={({ id }) => this.onSelectBranch(id, root)}
        onPrefetch={id => root.order.prefetchFoodMenu(id)}
      />
    )
  }
  renderQuery = (root: TRootModel) => {
    const { branchId } = this.props.match.params
    if (root.business.isBrand) {
      return (
        <QueryPickupMapBrand
          variables={{ brandId: root.business.brandId! }}
          renderLoading={!branchId}
          render={({ branches }) => {
            const branch =
              (branches && branches.find(RM.propEq('id', branchId))) || null
            return (
              <>
                {this.renderHeader(branch)}
                {branch
                  ? this.renderMap(branch, root)
                  : this.renderList(branches, root)}
              </>
            )
          }}
        />
      )
    }
    return (
      <QueryPickupMapBranch
        variables={{ branchId: root.business.branchId! }}
        renderLoading={false}
        render={({ branch }, { loading }) => (
          <>
            {this.renderHeader(branch)}
            {this.renderMap(branch, root)}
          </>
        )}
      />
    )
  }
  render() {
    return <WithRoot render={this.renderQuery} />
  }
}
