import './MobilePaymentMethod.css'

import { i18n } from 'i18n'
import React from 'react'

import { PaymentGateEnum } from 'graph/types'
import { TOrderModel } from 'models/OrderModel'

import { MobilePaymentButton } from './MobilePaymentButton'

interface IProps {
  orderModel: TOrderModel
  onConfirm(): void
}

export const MobilePaymentMethod: React.SFC<IProps> = ({
  orderModel,
  onConfirm,
}) => (
  <div className="mobile__confirm__payment__method">
    <MobilePaymentButton
      method={PaymentGateEnum.GOPAY}
      onClick={onConfirm}
      data-testid="mobilePaymentMethodGoPay"
    >
      <span className="flaticon-credit-card">
        {i18n.t`Pay by card`.toLocaleUpperCase()}
      </span>
    </MobilePaymentButton>
    <MobilePaymentButton
      method={PaymentGateEnum.CASH}
      onClick={onConfirm}
      data-testid="mobilePaymentMethodCash"
    >
      <span className="flaticon-money">
        {i18n.t`Pay by cash`.toLocaleUpperCase()}
      </span>
    </MobilePaymentButton>
  </div>
)
