import { buildQuery, gql } from 'graph'

import { GSmartBanner } from 'graph/types'

const BannerQuery = gql`
  query GSmartBanner($branchId: ID!, $platform: MobilePlatformEnum!) {
    app: mobileApplicationSettings(
      companyBranch: $branchId
      platform: $platform
    ) {
      title
      url
      icon
    }
  }
`

export const QueryBanner = buildQuery<
  GSmartBanner.Query,
  GSmartBanner.Variables
>(BannerQuery)
