import './MobileCart.css'
import './MobileSlidingCart.css'

import classNames from 'classnames'
import React, { Component } from 'react'
import { Redirect } from 'react-router'

import { CartView } from '../cart/CartView'
import { OrderSubmitLine } from '../orderForm/OrderSubmitLine'
import { QueryMobileCart } from './MobileCart.graphql'
import { MobileCartSummary } from './MobileCartSummary'

interface IProps {
  count: number
  price: string
  isLoading: boolean
}

interface IState {
  isOpen: boolean
  goConfirm: boolean
}

class MobileCartController extends Component<IProps, IState> {
  state = { isOpen: false, goConfirm: false }

  setOpen = (isOpen: boolean) => {
    this.setState({ isOpen })
  }

  static getDerivedStateFromProps({ count }: IProps, { isOpen }: IState) {
    if (count === 0 && isOpen === true) {
      return {
        isOpen: false,
      }
    }
    return null
  }

  // shouldComponentUpdate({ isLoading }: IProps) {
  //   return !isLoading
  // }

  get isEmpty() {
    return this.props.count === 0
  }

  onToggle = () => {
    if (this.isEmpty) {
      this.setOpen(false)
    } else {
      this.setState(prev => ({ isOpen: !prev.isOpen }))
    }
  }

  onSubmit = () => {
    this.setState({ goConfirm: true })
  }

  render() {
    const { count, price, isLoading } = this.props
    const { isOpen, goConfirm } = this.state

    if (goConfirm) {
      return <Redirect to="/confirm" push={true} />
    }

    return (
      <div
        className={classNames('mobile__sliding__cart', {
          mobile__sliding__cart__open: isOpen,
        })}
      >
        <div className="mobile__cart__view">
          <MobileCartSummary
            count={count}
            price={price}
            isLoading={isLoading}
            onClick={this.onToggle}
          />
          {isOpen ? (
            <>
              <CartView />
              <OrderSubmitLine
                canSubmit={!this.isEmpty}
                onClick={this.onSubmit}
              />
            </>
          ) : null}
        </div>
      </div>
    )
  }
}

export const MobileCart: React.SFC<{ orderId: ID }> = ({ orderId }) => (
  <QueryMobileCart
    variables={{ orderId }}
    renderLoading={false}
    render={({ order }, { loading }) => (
      <MobileCartController
        count={order ? order.countWares : 0}
        price={order ? order.totalSum.formattedValue : '???'}
        isLoading={loading || !order}
      />
    )}
  />
)
