import './MobilePickupList.css'

import { i18n } from 'i18n'
import React from 'react'
import styled from 'styled-components'

import { PrimaryButton, SecondaryButton } from 'common/Buttons'
import { ShopAddress } from 'common/ShopAddress'
import { GPickupBranch } from 'graph/types'

import { ShopStatusDisplay } from '../shop/ShopStatusDisplay'

interface IProps {
  branches: GPickupBranch.Fragment[]
  onSelect(site: GPickupBranch.Fragment): void
  onShowMap(site: GPickupBranch.Fragment): void
  onPrefetch(branchId: ID): void
}

const StatusDisplayPosition = styled.div``

const MobilePickupList: React.SFC<IProps> = ({
  branches,
  onSelect,
  onShowMap,
  onPrefetch,
}) => {
  const renderBranch = (branch: GPickupBranch.Fragment) => {
    return (
      <div
        className="mobile__pickup__list__item"
        data-testid="MobilePickupList"
        key={branch.id}
      >
        <div className="mobile__pickup__list__header">{branch.name}</div>
        <ShopAddress address={branch.address} />
        <StatusDisplayPosition>
          <ShopStatusDisplay status={branch} />
        </StatusDisplayPosition>

        <div className="mobile__pickup__list__buttons">
          <SecondaryButton onClick={() => onShowMap(branch)}>
            <span className="flaticon-signs">
              {i18n.t`Show on map`.toLocaleUpperCase()}
            </span>
          </SecondaryButton>
          {branch.pickupEnabled ? (
            <PrimaryButton
              onClick={() => onSelect(branch)}
              onMouseEnter={() => onPrefetch(branch.id)}
            >
              <span className="flaticon-mark">
                {i18n.t`Choose`.toLocaleUpperCase()}
              </span>
            </PrimaryButton>
          ) : (
            <PrimaryButton disabled={true}>
              <span className="flaticon-mark">
                {i18n.t`Delivery only`.toLocaleLowerCase()}
              </span>
            </PrimaryButton>
          )}
        </div>
      </div>
    )
  }

  return (
    <div className="mobile__pickup__list">{branches.map(renderBranch)}</div>
  )
}

export default MobilePickupList
