import './MobileOrderView.css'

import React from 'react'

import { WithOrder } from '../../common/WithOrder'
import { AddToCartModal } from '../cart/AddToCartModal'
import { FoodMenuView } from '../foodMenu/FoodMenuView'
import { MobileCart } from './MobileCart'
import { MobileClosingTimeModal } from './MobileClosingTimeModal'

export const MobileOrderView = props => (
  <WithOrder
    redirect={true}
    render={order => (
      <div className="mobile__order__view">
        <FoodMenuView />
        <MobileCart orderId={order.orderId!} />
        <AddToCartModal order={order} />
        <MobileClosingTimeModal branchId={order.branchId!} />
      </div>
    )}
  />
)
